<template lang="html">
  <div class="_full_router fundDetailBody">
    <custHeader :title="title"></custHeader>
    <mt-navbar v-model="selected">
      <mt-tab-item id="1">资产情况</mt-tab-item>
      <mt-tab-item id="2">资产统计</mt-tab-item>
    </mt-navbar>

    <mt-tab-container v-model="selected">
      <mt-tab-container-item id="1">
        <ul class="fund-ul" style="">
          <li class="fund-li" v-for="(item,index) in fundData">
            <div class="height30" style="font-size: 13px;">
              {{ item.name }} （元）
            </div>
            <div class="height30">
              {{ item.value | plusZero}}
            </div>
          </li>

        </ul>
      </mt-tab-container-item>
      <mt-tab-container-item id="2">
        <ul class="fund-ul" style="">
          <li class="fund-li" v-for="(item,index) in subjectData">
            <div class="height30"  style="font-size: 13px;">
               {{ item.name }}（元）
            </div>
            <div class="height30">
              {{ item.value | plusZero}}
            </div>
          </li>

        </ul>
      </mt-tab-container-item>
    </mt-tab-container>

  </div>
</template>

<script>
import custHeader from  'components/custHeader'
import {Navbar, TabItem} from 'mint-ui'
Vue.component(Navbar.name,Navbar)
Vue.component(TabItem.name,TabItem)
export default {
  data(){
    return {
      title:"资产详情",
      selected:"1",
      subjectData:[],
      fundData:[],
      // subjectDataTran:["累计平仓盈亏","累计导入资金","累计出入金扎差","累计促销返现","计提购物金","","累计购物返现","累计现金转购物金","累计购物金支付"],
      subjectDataTran:["平仓盈亏","累计购物金","出入金扎差","计提购物金","累计导入资金","现金转购物金","促销返现","购物金支付","购物返现"],
      subjectDataEn:["closeProfitAmt","shopAmt","netTranAmt","shopAccruedAmt","impAmt","shopChgAmt","promotionCashback","shopPayAmt","shopCashback"],
      // fundDataTran:["计提购物金","资金可用","资金余额","长期冻结金额","中期冻结金额","昨日余额","购物金","交易冻结金额","在途资金","资金可取","持有市值","总资产"],
      fundDataTran:["总资产","中期冻结金额","总市值","交易冻结金额","资金余额","长期冻结金额","资金可取","购物金","在途资金"],
      fundDataEn:["totalAssets","fundMidFrz","mktVal","fundTrdFrz","fundBln","fundFrz","fundWithDraw","fundShopAmt","fundUncomeAmt"]
    }
  },
  created:function(){
    var self = this;
    axiosUtil.qryCustSumAsset().then(data => {
      var data = data.data.data;
      // var dataKey = Object.keys(data)
      for (var i = 0; i < self.fundDataEn.length; i++) {
        if(self.fundDataEn[i] == "fundWithDraw"){
          self.fundData.push({name:self.fundDataTran[i],value:data[self.fundDataEn[i]]*1>0?data[self.fundDataEn[i]]:0})
        }else{
          self.fundData.push({name:self.fundDataTran[i],value:data[self.fundDataEn[i]]})
        }
        
      }
    })
    axiosUtil.qryCustSubject().then(data => {
      var data = data.data[0];
      delete data.currency;
      // var dataKey = Object.keys(data)
      for (var i = 0; i < self.subjectDataEn.length; i++) {
        self.subjectData.push({name:self.subjectDataTran[i],value:data[self.subjectDataEn[i]]})
      }

    })
  },
  components:{
    custHeader
  }
}
</script>

<style lang="css" scoped>
.fund-ul{
  display:flex;
  flex-wrap: wrap;
  margin-top:20px;
}
.height30{
  text-align: center;
  height: 30px;
  line-height: 30px;
}
.fund-li{
  width: 50%;
  padding-bottom: 2vmin;
  border-bottom: 1px solid #eee;
  border-right: 1px solid #eee;
  padding-top: 2vmin;
}
.fund-li:nth-child(1),.fund-li:nth-child(2){
  border-top: 1px solid #eee;
}
.fundDetailBody{

}
</style>



